html,
body,
#root,
.App {
  height: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 4rem auto;
  padding: 2rem 3rem 3rem;
  max-width: 500px;
  background: rgb(149, 230, 196);
  color: rgba(255, 255, 255);
  cursor: default;
}

h1,
p {
  margin: 0;
}

.list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  background: rgb(141, 219, 186);
  margin: 10px 0;
  border: 1px solid rgba(255, 255, 255);
  color: rgba(255, 255, 255);
  height: 2rem;
}

.true {
  color: rgb(225, 225, 225);
  text-decoration: line-through rgb(123, 167, 149);
}
.todoButtons {
  display: flex;
  height: 2rem;
}

.todoButtons button {
  padding: 0 1rem;
  border: none;
  background: rgb(141, 219, 186);
  color: white;
  font-weight: bold;
  margin-left: 5px;
}

.todoButtons button svg {
  display: flex;
}

.activeButton:hover {
  cursor: pointer;
}

.todoButtons .activeButton:hover svg {
  fill: rgb(96, 164, 136);
}

.disabled:hover {
  cursor: not-allowed;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.newTodo {
  padding-top: 30px;
}

.newTodo h2 {
  margin: 5px 0;
}

.inputButton {
  display: flex;
  height: 3rem;
}

.inputButton input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  padding: 0 1em;
  font-size: initial;
  color: rgb(87, 153, 125);
}

.inputButton input:focus {
  outline: none;
  border: 1px solid rgb(87, 153, 125);
}

.inputButton button {
  padding: 0 1.3rem;
  border: none;
  background: rgb(141, 219, 186);
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255);
  margin-left: 5px;
}

.activeAdd:hover {
  background: rgb(124, 195, 165);
  cursor: pointer;
}

.inputButton button:disabled {
  cursor: not-allowed;
  background: rgb(176, 227, 205);
}

@media screen and (max-width: 420px) {
  .container {
    max-width: 420px;
    height: 100%;
    max-height: 914px;
    box-sizing: border-box;
    margin: 0;
    padding: 2rem 1rem;
  }

  h1 {
    margin: 0;
  }

  .todoList {
    max-height: 690px;
    overflow: scroll;
  }

  .newTodo {
    padding-top: 0;
  }
  .todoButtons button {
    padding: 0;

    margin-left: 3em;
  }
  .todoButtons button svg {
    width: 2em;
    height: 2em;
    padding: 0 5px;
  }
}
